import Vue from 'vue'
import VueRouter from 'vue-router'


import Inicio from '../views/Inicio.vue'
import Creadores_estrella from '../views/Creadoresestrella.vue'
import Dotcsv from '../views/Dotcsv.vue'
import Fazt from '../views/Fazt.vue'
import GioCode from '../views/GioCode.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'inicio',
    component: Inicio
  },
  {
    path: '/creadoresestrella',
    name: 'creadoresestrella',
    component: Creadores_estrella
  },
  {
    path: '/dotcsv',
    name: 'dotcsv',
    component: Dotcsv
  },
  {
    path: '/fazt',
    name: 'fazt',
    component: Fazt
  },
  {
    path: '/giocode',
    name: 'giocode',
    component: GioCode
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "active"
})

export default router
